import React, { useState } from 'react';
import CardDesign from '../Preview/components/CardDesign/CardDesign';
import Drawer from '../Preview/components/Drawer/Drawer';
import Preview from '../Preview/Preview';
import MainCanvas from "./MainCanvas"
import CardHeader from './tailwindComponents/CardHeader/CardHeader';

const Card: React.FC = (): JSX.Element => {
    const [open, setOpen] = useState(false);

    return (
        <div className="h-full">
            <div>
                <CardHeader />
            </div>
            <div>

                <MainCanvas />
            </div>
            <div className="z-50">
                <Preview setIsOpen={setOpen} />
                <Drawer isOpen={open} setIsOpen={setOpen}><CardDesign /></Drawer>
            </div >
        </div>
    )
};
export default Card;
